import styles from "app/styles/global.module.css";
import Logo from "assets/logo_transparent.png";
import { Link } from "react-router";

type NavLinkProps = {
  href: string;
  label: string;
};

const NavLinkCollection: NavLinkProps[] = [
  { href: "/submitrepair", label: "Submit a Repair" },
  { href: "/pricing", label: "Pricing" },
  { href: "/about", label: "About" },
  { href: "/contact", label: "Contact" },
];

export const NavBar: React.FC = () => {
  return (
    <div style={{ margin: "1.5vh" }} className={styles.navbar}>
      <Link to="/">
        <img src={Logo} className={styles.navbar_logo} alt="Company Logo" />
      </Link>{" "}
      <div className={styles.navbar_links}>
        {NavLinkCollection.map((value, index) => {
          return <NavLink href={value.href} label={value.label} />;
        })}
      </div>
    </div>
  );
};

const NavLink: React.FC<NavLinkProps> = ({ href, label }) => {
  return (
    <span>
      <Link className={styles.navlink} to={href}>
        <span>{label}</span>
      </Link>
    </span>
  );
};
